.paper
{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    
   
}
.pa-texts {
    width: 65%;
    
  }
  
  .pa-title {
    font-size: 50px;
    font-weight: 600;
    
  }
  
  .pa-desc {
    margin: 20px 0px;
    
  }
  .paper-title
  {
      font-size: 30px;
      font-weight: 300;
      
  }
  .paper-i{
      
      font-size: 20px;
      text-decoration: none;
      
  }
  table {
    border: 2px solid rgb(28, 155, 24);
    width: auto;
    height: auto;
    border-radius: 10px;
    border-width: 4px;
    
  }
    
  th {
    border-bottom: 1px solid rgb(28, 155, 24);
    font-size: 20px;
    font-weight: 600;
    color: white;
  }
    
  td {
    font-size: 30px;
    text-align: center;
    font-weight: 600;
    text-align: justify;
    /* margin-left: 20px;
    margin-right: 20px;
    border-style: solid;
    border-radius: 10px; */
  
  }
  .s-img
  {
    height: 80px;
    width: 100px;
    border-radius: 10px;
    border-color: black; 
  }